<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="2">
                    <router-link to="/trace/batch">
                        <el-menu-item index="0">
                            <i class="el-icon-position"></i>
                            <span slot="title">溯源管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/certify">
                        <el-menu-item index="1">
                            <i class="el-icon-circle-check"></i>
                            <span slot="title">认证管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/detect">
                        <el-menu-item index="2">
                            <i class="el-icon-set-up"></i>
                            <span slot="title">检测管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/process">
                        <el-menu-item index="3">
                            <i class="el-icon-setting"></i>
                            <span slot="title">加工管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/delivery">
                        <el-menu-item index="4">
                            <i class="el-icon-truck"></i>
                            <span slot="title">配送管理</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-select class="m-r-15" size="small" v-model="vm.searchType" placeholder="请选择检测项目">
                        <el-option
                            v-for="item in typeList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="搜索检测产品名称" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="search">搜索</el-button>
                </div>
                <el-button type="primary" size="small" @click="add">添加检测记录</el-button>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="dataList" size="small" border stripe style="width: 100%">
                        <el-table-column prop="product" label="检测产品"></el-table-column>
                        <el-table-column prop="batch" label="产品批次"></el-table-column>
                        <el-table-column prop="detectProject" label="检测项目"></el-table-column>
                        <el-table-column prop="detectOrg" label="检测机构"> </el-table-column>
                        <el-table-column prop="detectUser" label="检测员"> </el-table-column>
                        <el-table-column prop="detectTime" label="检测时间"></el-table-column>
                        <el-table-column fixed="right" label="操作" width="140">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" icon="el-icon-search" @click="edit(scope.row)"></el-button>
                                <el-button type="danger" size="mini" icon="el-icon-delete" @click="remove(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="检测记录" :visible.sync="dialogVisible" width="1000px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="所属地块" prop="plot">
                        <el-input style="width:260px;" v-model="form.plot" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="检测时间" prop="detectTime">
                        <el-date-picker style="width:260px;" type="date" placeholder="选择日期" v-model="form.time"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="检测产品" prop="product">
                        <el-select style="width:260px;" v-model="form.product" filterable placeholder="请选择检测产品">
                            <el-option
                                v-for="item in productList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="产品批次" prop="batch">
                        <el-select style="width:260px;" v-model="form.batch" filterable placeholder="请选择产品批次">
                            <el-option
                                v-for="item in batchList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="检测项目" prop="detectProject">
                        <el-select style="width:260px;" v-model="form.detectProject" filterable placeholder="请选择检测项目">
                            <el-option
                                v-for="item in kindList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="检测明细" prop="detectDetail">
                        <el-table :data="form.detectDetail" size="mini" border style="width: 100%">
                            <el-table-column prop="target" label="检测指标">
                                <template slot-scope="scope">
                                    <el-select size="mini" v-model="scope.row.target" placeholder="请选择">
                                        <el-option
                                            v-for="item in targetList"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                        </el-option>
                                    </el-select>
                                </template>
                            </el-table-column>
                            <el-table-column prop="value" label="检测值">
                                <template slot-scope="scope">
                                    <el-input size="mini" v-model="scope.row.value"></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="standard" label="检测标准">
                                <template slot-scope="scope">
                                    <el-input size="mini" v-model="scope.row.standard" readonly></el-input>
                                </template>
                            </el-table-column>
                            <el-table-column prop="" label="操作" width="80px">
                                <template slot-scope="scope">
                                    <el-button type="danger" @click="removeRow(scope.$index)" size="mini">删除</el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-button size="mini" type="text" class="m-t-5" @click="addRow">+ 添加</el-button>
                    </el-form-item>
                    <el-form-item label="检测机构" prop="detectUserOrg">
                        <el-input style="width:260px;" v-model="form.detectUserOrg"></el-input>
                    </el-form-item>
                    <el-form-item label="检测员" prop="detectUser">
                        <el-input  style="width:260px;" v-model="form.detectUser"></el-input>
                    </el-form-item>
                    <el-form-item label="检测报告图片" prop="detectPic">
                        <el-upload
                            multiple
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :limit="10"
                            :file-list="form.detectPic">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchKey: '',
                searchType: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            typeList: [{
                value: '选项1',
                label: ''
            }, {
                value: '选项2',
                label: '双皮奶'
            }, {
                value: '选项3',
                label: '蚵仔煎'
            }, {
                value: '选项4',
                label: '龙须面'
            }, {
                value: '选项5',
                label: '北京烤鸭'
            }],
            dataList: [{
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                detectProject: '农残检测',
                detectOrg: 'XXX检测机构',
                detectUser: '黄芳',
                detectTime: '2018-06-14',
            }, {
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                detectProject: '农残检测',
                detectOrg: 'XXX检测机构',
                detectUser: '黄芳',
                detectTime: '2018-06-14',
            }, {
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                detectProject: '农残检测',
                detectOrg: 'XXX检测机构',
                detectUser: '黄芳',
                detectTime: '2018-06-14',
            }, {
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                detectProject: '农残检测',
                detectOrg: 'XXX检测机构',
                detectUser: '黄芳',
                detectTime: '2018-06-14',
            }],
            dialogVisible: false,
            form:{
                plot: '东里旺旺葡萄园',
                detectTime: '',
                product: '',
                batch: '',
                detectProject: '',
                detectDetail: [
                    {
                        target: 1,
                        value: '',
                        standard: '<=10μg/KG'
                    }
                ],
                detectUserOrg: '',
                detectUser: '',
                detectPic: []
            },
            targetList: [
                {
                    value: 1,
                    label: '黄曲霉毒素'
                }
            ],
            productList:[],
            batchList:[],
            kindList:[],
            rules: {
                product: [
                    { required: true, message: '请选择检测产品', trigger: 'change' },
                ],
                batch: [
                    { required: true, message: '请选择产品批次', trigger: 'change' }
                ],
                detectProject: [
                    { required: true, message: '请选择检测项目', trigger: 'change' }
                ],
                detectDetail: [
                    { type: 'array', required: true, message: '请选择检测明细', trigger: 'change' }
                ],
                detectUser: [
                    { required: true, message: '检测员', trigger: 'blur' }
                ]
            }
        }
    },
    computed:{
        tableHeight() {
            return document.documentElement.clientHeight - 275;
        }
    },
    methods:{
        search(){

        },
        add(){
            this.dialogVisible = true
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        remove(row){
            this.$confirm('是否删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                          
            });
        },
        resetForm() {
            this.form = {
                plot: '东里旺旺葡萄园',
                detectTime: '',
                product: '',
                batch: '',
                detectProject: '',
                detectDetail: [],
                detectUserOrg: '',
                detectUser: '',
                detectPic: []
            }
        },
        addRow(){
            if( this.form.detectProject == ''){
                this.$message({
                    message: '请选择检测项目',
                    type: 'warning'
                });
            }else{
                let item = {
                    target: '',
                    value: '',
                    standard: ''
                }
                this.form.detectDetail.push(item)
            }
        },
        removeRow(index){
            this.form.detectDetail.splice(index,1)
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


